import Vue from 'vue';
import Playlist from "./Playlist";

let apiPrefix = 'https://music-api.laiweb.org';

function getSubtitles(music) {
    if (music.lrc_path) {
        return parseLrc(music);
    } else if (music.srt_path) {
        return parseSrt(music);
    } else {
        return [];
    }
}

function parseLrc(music) {
    return fetch(apiPrefix + '/musics/' + music.id + '/subtitles').then(res => res.json()).then(res => {
        let data = res.data.split('\n');
        let output = [];
        for (let i = 0; i < data.length; i++) {
            let text = data[i];
            let durations = [];
            let matches = text.matchAll(/\[(\d\d):(\d\d).(\d\d)\]/g);
            let d = [60, 1, 0.01];
            for (let match of matches) {
                text = text.replace(match[0], '');
                let time = match.map(x => +x).slice(1).map((x, i) => x * d[i]).reduce((x, y) => x + y);
                durations.push(time);
            }

            durations.forEach(duration => {
                output.push({
                    start: duration,
                    text
                });
            });
        }
        output.sort((x, y) => x.start - y.start);
        for (let i = 0; i < output.length - 1; i++) {
            output[i].end = output[i + 1].start;
        }
        output[output.length - 1].end = music.duration;
        return output;
    })
}

function parseSrt(music) {
    return fetch(apiPrefix + '/musics/' + music.id + '/subtitles').then(res => res.json()).then(res => {
        let data = res.data.split('\n');

        let output = [];
        for (let i = 0; i < data.length; i += 4) {
            if (data[i]) {
                let text = data[i + 2];
                let match = [...data[i + 1].matchAll(/(\d\d):(\d\d):(\d\d),(\d\d\d) --> (\d\d):(\d\d):(\d\d),(\d\d\d)/g)][0].map(x => +x);
                let d = [3600, 60, 1, 0.001];
                let start = match.slice(1, 5).map((x, i) => x * d[i]).reduce((x, y) => x + y);
                let end = match.slice(5, 9).map((x, i) => x * d[i]).reduce((x, y) => x + y);
                output.push({
                    start, end, text
                });
            }
        }
        output.sort((x, y) => x.start - y.start);
        return output;
    })
}

let store = new Vue({
    data: {
        musics: [],
        listMusics: [],
        albums: [],
        music: null,
        playlist: new Playlist(),
        loading: true,
        modalText: '',
        mediaSession: [],
    },
    methods: {
        updateMediaSession() {
            this.mediaSession = this.playlist.musics.map(music => {
                let album = this.albums.find(album => album.title === music.albums[0]);
                let artwork = [];
                if (album) {
                    artwork = [{
                        sizes: '512x512',
                        src: album.cover_path,
                        type: 'image/jpeg'
                    }];
                }

                return {
                    artwork,
                    album: music.albums.join(', ') || '無',
                    title: music.title,
                    artist: music.artists.join(', ') || '不明'
                };
            });

            this.updateMetadata();
        },

        updateMediaPosition() {
            navigator.mediaSession.setPositionState({
                duration: this.music?.el.duration,
                currentTime: this.music?.el.currentTime,
                playbackRate: this.music?.el.playbackRate
            });
        },

        updateMetadata() {
            let track = this.mediaSession[this.playlist.indexOf(this.playlist.music)];
            navigator.mediaSession.metadata = new MediaMetadata({...track});
            this.updateMediaPosition();
        },


        setModalText(text = '') {
            this.modalText = text;
        },

        setMusic(music = null, time = 0) {
            if (this.music?.el) {
                this.pauseMusic();
                this.music.currentTime = 0;
            }

            this.music = music;
            if (this.music?.el) {
                this.music.currentTime = time;
                this.updateMediaSession();
            }
        },

        playIs(music) {
            return this.music === music && this.playlist.music !== music;
        },

        playMusic() {
            if (this.music?.el) {
                this.music.el.play();
                this.music.playing = true;
            } else {
                this.setModalText('音樂' + this.music.title + '無法播放!');
            }
        },

        pauseMusic() {
            if (this.music?.el) {
                this.music.el.pause();
                this.music.playing = false;
            }
        },

        switchPlayStatus() {
            if (this.music?.el) {
                if (this.music.playing) {
                    this.pauseMusic();
                } else {
                    this.playMusic();
                }
            }
        },


    },
    created() {
        Vue.prototype.init = Promise.all([
            fetch(apiPrefix + '/albums').then(res => res.json()),
            fetch(apiPrefix + '/musics').then(res => res.json()),
        ]).then(async res => {
            let albums = res[0].data;
            let musics = res[1].data;

            await Promise.all(musics.map(async music => {
                music.albums = albums.filter(album => album.musics.some(m => m.id === music.id)).map(album => album.title);
                music.subtitle = await getSubtitles(music);

                return new Promise(resolve => {
                    let el = new Audio();
                    el.crossOrigin = 'anonymous';
                    el.src = apiPrefix + '/' + music.source;
                    music.currentTime = 0;
                    music.playing = false;

                    el.onloadedmetadata = () => {
                        music.duration = el.duration;
                        music.el = el;
                        music.volume = 0.5;
                        resolve(music);
                    }

                    el.onerror = () => {
                        music.el = null;
                        music.duration = 0;
                        music.volume = 0.5;

                        resolve(music);
                    }

                    el.ontimeupdate = () => {
                        music.currentTime = el.currentTime;
                    }

                    el.onended = () => {
                        this.playlist.playNext();
                    }
                })
            }))

            albums = albums.map(album => {
                album.musics = album.musics.map(music => musics.find(m => m.id === music.id));
                album.cover_path = apiPrefix + '/' + album.cover_path;
                let artists = new Set();
                album.musics.forEach(music => {
                    music.artists.forEach(artist => {
                        artists.add(artist);
                    })
                })
                album.artists = [...artists];
                album.duration = album.musics.map(music => music.duration).reduce((x, y) => x + y);
                return album;
            })

            this.musics = musics;
            this.listMusics = musics;
            this.albums = albums;

            let playlist = JSON.parse(localStorage.playlist || '[]');
            playlist = playlist.map(p => musics.find(music => music.id === p.id));
            this.playlist = new Playlist(playlist);
            this.loading = false;
            this.updateMediaSession();


            navigator.mediaSession.setActionHandler('previoustrack', () => {
                this.playlist.playPrev();
            });

            navigator.mediaSession.setActionHandler('nexttrack', () => {
                this.playlist.playNext();
            });

            navigator.mediaSession.setActionHandler('play', () => {
                this.switchPlayStatus();
            });

            navigator.mediaSession.setActionHandler('pause', () => {
                this.switchPlayStatus();
            });


        })
    },
})

function mapGetter(...keys) {
    let output = {};
    for (let key of keys) {
        output[key] = () => store[key];
    }
    return output;
}

export {mapGetter};

export default store;
