<template>
    <div id="loading" v-show="$store.loading">
        <div class="box"></div>
        <div class="box"></div>
        <div class="box"></div>
    </div>
</template>

<script>
    export default {
        name: "Loading"
    }
</script>

<style scoped>
    #loading {
        position: fixed;
        top: 0;
        left: 0;
        background-color: #5555;
        width: 100vw;
        height: 100vh;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    .box {
        border-radius: 50%;
        width: 20px;
        height: 20px;
        margin: 5px;
        background-color: #39f;
        animation: loading 1s infinite alternate;
    }

    .box:nth-child(1) {
        animation-delay: -.6s;
    }

    .box:nth-child(2) {
        animation-delay: -.3s;
    }

    @keyframes loading {
        from {
            transform: translateY(-20px);
        }
        to {
            transform: translateY(0);
        }
    }
</style>
