<template>
    <div v-if="music">
        <div style="display: flex;">
            <input style="flex:1; margin-right: 10px;" type="range" min="0" :max="music.duration"
                   v-model="music.currentTime">
            {{music.currentTime|duration}} / {{music.duration |duration }}
        </div>

        <div style="text-align:right;">
            音量：<input type="range" min="0" max="1" step="0.01" v-model="music.volume">
        </div>
    </div>
</template>

<script>
    import {mapGetter} from "../store";

    export default {
        name: "Player-Control",
        computed: {
            ...mapGetter('music')
        },
        watch: {
            'music.currentTime': function (val) {
                this.music.el.currentTime = val;
            },
            'music.volume': function (val) {
                this.music.el.volume = val;
            }
        }
    }
</script>

<style scoped>
    #player-control {
        display: flex;
        flex-direction: column;
        justify-content: space-around;
    }


</style>
