import store from './store';


class Playlist {
    constructor(musics = []) {
        this.musics = musics;
        this.open = false;
        this.music = null;
    }

    reset() {
        this.music = null;
        store.setMusic(null);
    }

    set(music) {
        this.music = music;
        store.setMusic(music);
    }

    clear() {
        this.musics.length = 0;
        this.reset();
        this.save();
    }

    save() {
        localStorage.playlist = JSON.stringify(this.musics);
    }

    add(music) {
        if (this.contains(music)) {
            store.setModalText('音樂已經存在播放清單!');
        } else {
            if (music.el) {
                this.musics.push(music);
                this.save();
                this.open = true;
            } else {
                store.setModalText('音樂' + music.title + '無法播放,因此無法加入播放清單!');
            }
        }
    }

    remove(music) {
        let index = this.indexOf(music);
        this.musics.splice(index, 1);
    }

    indexOf(music) {
        return this.musics.indexOf(music);
    }

    contains(music) {
        return this.indexOf(music) !== -1;
    }

    playNext() {
        let index = this.indexOf(this.music);
        if (index + 1 < this.musics.length) {
            this.set(this.musics[index + 1]);
            store.playMusic();
        }
    }

    playPrev() {
        let index = this.indexOf(this.music);
        if (index - 1 >= 0) {
            this.set(this.musics[index - 1]);
            store.playMusic();
        }
    }

    isFirst() {
        return this.indexOf(this.music) - 1 < 0;
    }

    isLast() {
        return this.indexOf(this.music) + 1 >= this.musics.length;
    }

    get playing() {
        return this.music?.playing;
    }


}

export default Playlist;
