<template>
    <div v-touch:y>
        <div class="albums">
            <router-link :to="`/albums/${album.id}`" tag="div" class="album" v-for="(album, i) in albums" :key="i">
                <img draggable="false" :src="album.cover_path">
                <div>{{album.title}}</div>
                <div>演唱者： {{album.artists.join(', ') || '不明'}}</div>
            </router-link>
        </div>
    </div>
</template>

<script>
    import {mapGetter} from "../store";

    export default {
        name: "Albums",
        computed: {
            ...mapGetter('albums')
        }
    }
</script>

<style scoped>
    .albums {
        display: grid;
        grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    }

    .album {
        grid-area: unset !important;
        margin: 5px;
        border: 1px solid #5555;
        cursor: pointer;
        transition: .5s;
    }

    .album:hover {
        background-color: #5555;
    }
</style>
