<template>
    <div v-touch:y>
        <div class="musics">
            <input type="text" v-model="searchText" placeholder="Search...">

            <router-view></router-view>

            <div class="music" v-for="(music, i) in musics" :key="i">
                <button class="play-btn" @click="play(music)">
                    {{ $store.playIs(music) && music.playing? '&#x23f8;':'&#x23f5;'}}
                </button>
                <div class="title">
                    <span v-for="(t, i) in music.title" :key="i"
                          :class="{active: payload.title.toLowerCase().includes(t.toLowerCase())}">{{t}}</span>
                </div>
                <div class="artist">演唱者：
                    <span v-for="(t, i) in music.artists.join(', ') || '不明'" :key="i"
                          :class="{active: payload.artist.toLowerCase().includes(t.toLowerCase())}">{{t}}</span>
                </div>
                <div class="album">專輯：
                    <span v-for="(t, i) in music.albums.join(', ') || '無'" :key="i"
                          :class="{active: payload.album.toLowerCase().includes(t.toLowerCase())}">{{t}}</span>
                </div>
                <div class="duration">{{music.duration | duration}}</div>
                <button class="add-btn" @click="playlist.add(music)">+</button>
            </div>
        </div>
        <Modal @close="continuePlayOpen=false" v-show="continuePlayOpen">
            要繼續播放嗎?
            <div class="btn-group">
                <button class="primary" @click="continuePlay()">Yes</button>
                <button class="danger" @click="continuePlayOpen=false">No</button>
            </div>
        </Modal>
    </div>
</template>

<script>
    import {mapGetter} from "../store";

    export default {
        name: "Musics",
        data() {
            return {
                searchText: '',
                payload: {
                    title: '',
                    album: '',
                    artist: '',
                    composer: '',
                },
                continuePlayOpen: false
            }
        },
        methods: {
            play(music) {
                if (this.$store.playIs(music)) {
                    this.$store.switchPlayStatus();
                    if (music.playing) {
                        if (this.$route.name === 'albums') {
                            this.$router.replace(`/albums/${this.$route.params.albumId}/${music.id}`).catch(() => {
                            });
                        }
                    } else {
                        if (this.$route.name === 'albums') {
                            this.$router.replace(`/albums/${this.$route.params.albumId}`).catch(() => {
                            });
                        }
                    }
                } else {
                    if (this.$route.name === 'albums') {
                        this.$router.replace(`/albums/${this.$route.params.albumId}/${music.id}`).catch(() => {
                        });
                    }
                    this.playlist.reset();
                    this.$store.setMusic(music);
                    this.$store.playMusic();
                }
            },
            continuePlay() {
                let id = +this.$route.params.musicId;
                let music = this.$store.musics.find(music => music.id === id);
                this.$store.setMusic(music);
                this.$store.playMusic();
                this.continuePlayOpen = false;
            }
        },
        computed: {
            ...mapGetter('playlist'),
            musics() {
                return this.$store.listMusics.filter(music => {
                    if (this.payload.title && music.title.toLowerCase().includes(this.payload.title.toLowerCase())) {
                        return true;
                    }

                    if (this.payload.artist && music.artists.some(artist => artist.toLowerCase().includes(this.payload.artist.toLowerCase()))) {
                        return true;
                    }

                    if (this.payload.album && music.albums.some(album => album.toLowerCase().includes(this.payload.album.toLowerCase()))) {
                        return true;
                    }

                    if (this.payload.composer && music.composers.some(composer => composer.toLowerCase().includes(this.payload.composer.toLowerCase()))) {
                        return true;
                    }

                    return this.searchText.length < 3;
                })
            }
        },
        mounted() {
            this.init.then(() => {
                if (this.$route.name === 'albums') {
                    let id = +this.$route.params.musicId;
                    let music = this.$store.musics.find(music => music.id === id);
                    if (music) {
                        this.continuePlayOpen = true;
                    }
                }
            })
        },
        watch: {
            searchText(val) {
                let payload = {
                    title: '',
                    album: '',
                    artist: '',
                    composer: '',
                };
                this.payload = payload;

                if (val.length >= 3) {
                    let matches = [...val.matchAll(/(title|album|artist|composer):(?=")"([^"]+)|(title|album|artist|composer):(\S+)/g)];
                    if (matches.length) {
                        payload = Object.fromEntries(matches.map(x => x.filter(y => y).slice(1)))
                    } else {
                        payload.title = val;
                    }
                }

                Object.assign(this.payload, payload);
            }
        }
    }
</script>

<style scoped>
    .music {
        display: grid;
        grid-template:
        ". title duration ."
        "play-btn artist duration add-btn"
        ". album duration .";
        grid-template-columns: 40px 1fr 80px 40px;
        grid-gap: 10px;
        margin: 5px;
        padding: 5px;
        border-bottom: 1px solid #5555;
        align-items: center;
    }

    input {
        width: 100%;
        height: 30px;
        font-size: 18px;
        border-radius: 3px;
        outline: none;
        border: 1px solid #333;
    }

    .active {
        background-color: #ff6;
        color: #333;
    }
</style>
