<template>
    <div style="height: 100%;">
        <div v-show="music" style="height: 100%;display: flex;flex-direction: column;">
            <div v-if="music">當前播放： {{music.title}}</div>
            <canvas ref="cvs" style="flex:1;"></canvas>
            <div class="subtitle">{{subtitle}}</div>
            <div class="btn-group circles" v-if="music">
                <button @click="playlist.playPrev()" v-if="playlist.musics.length>1 && playlist.music"
                        :class="{disabled: playlist.isFirst()}">
                    &#x23ea;&#xfe0e;
                </button>
                <button style="width: 50px;height: 50px;" @click="$store.switchPlayStatus()">
                    {{ music.playing? '&#x23f8;':'&#x23f5;'}}
                </button>
                <button @click="playlist.playNext()" v-if="playlist.musics.length>1 && playlist.music"
                        :class="{disabled: playlist.isLast()}">
                    &#x23e9;&#xfe0e;
                </button>
            </div>
        </div>
        <div v-show="!music"
             style="display: flex;align-items: center;justify-content: center; height: 100%;font-size: 40px;">
            當前沒有播放的音樂
        </div>
        <Modal @close="continuePlayOpen=false" v-show="continuePlayOpen">
            要繼續播放嗎?
            <div class="btn-group">
                <button class="primary" @click="continuePlay()">Yes</button>
                <button class="danger" @click="continuePlayOpen=false">No</button>
            </div>
        </Modal>
    </div>
</template>

<script>
    import {mapGetter} from "../store";

    export default {
        name: "Player",
        data() {
            return {
                frame: 0,
                subtitle: '',
                cvs: null, ctx: null,
                continuePlayOpen: false
            }
        },
        methods: {
            setSize() {
                this.cvs.width = 0;
                this.cvs.height = 0;
                this.cvs.width = this.cvs.clientWidth;
                this.cvs.height = this.cvs.clientHeight;
            },

            render() {
                this.setSize();
                if (this.music?.el) {
                    this.frame++;
                    let time = this.music.currentTime;

                    if (this.frame % 100 === 0 && this.$route.name === 'playlist') {
                        let status = !this.music.playing ? 'paused' : 'playing';
                        this.$router.replace(`/playlist/${this.music.id}/${time}/${status}`).catch(() => {
                        });
                    }

                    if (!this.music.ctx) {
                        this.music.ctx = new AudioContext();
                        this.music.analyser = this.music.ctx.createAnalyser();
                        this.music.source = this.music.ctx.createMediaElementSource(this.music.el);
                        this.music.analyser.connect(this.music.ctx.destination);
                        this.music.source.connect(this.music.analyser);
                    }

                    this.subtitle = this.music.subtitle.find(s => s.start <= time && time <= s.end)?.text;


                    let data = new Uint8Array(this.music.analyser.frequencyBinCount);
                    this.music.analyser.getByteFrequencyData(data);
                    data = data.slice(0, 600);
                    let w = this.cvs.width / data.length;
                    let h = this.cvs.height / 255;

                    this.ctx.clearRect(0, 0, this.cvs.width, this.cvs.height);
                    for (let i = 0; i < data.length; i += 10) {
                        let x = w * i;
                        let y = data[i] * h;
                        this.ctx.save();
                        this.ctx.fillStyle = '#39f';
                        this.ctx.translate(x, this.cvs.height);
                        this.ctx.fillRect(0, 0, w * 10, -y);
                        this.ctx.restore();
                    }


                }

                requestAnimationFrame(this.render);
            },
            continuePlay() {
                let id = +this.$route.params.musicId;
                let music = this.$store.musics.find(music => music.id === id);
                let time = +this.$route.params.time;
                let status = this.$route.params.status;
                this.$store.setMusic(music, time);
                if (status === 'playing') {
                    this.$store.playMusic();
                }
                this.continuePlayOpen = false;
            },
        },
        computed: {
            ...mapGetter('music', 'playlist')
        },
        mounted() {
            this.init.then(() => {
                this.cvs = this.$refs.cvs;
                this.ctx = this.cvs.getContext('2d');
                this.setSize();

                if (this.$route.name === 'playlist') {
                    let id = +this.$route.params.musicId;
                    let music = this.$store.musics.find(music => music.id === id);
                    if (music) {
                        this.continuePlayOpen = true;
                    }
                    requestAnimationFrame(this.render);
                } else {
                    requestAnimationFrame(this.render);
                }
            })
        }
    }
</script>

<style scoped>
    .circles > button {
        border-radius: 50%;
        width: 40px;
        height: 40px;
        outline: none;
    }

    .subtitle {
        height: 40px;
        font-size: 24px;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    .disabled {
        opacity: .7;
        cursor: no-drop;
    }
</style>
