import Vue from 'vue'
import App from './App.vue'
import store from './store';
import Modal from "./components/Modal";

import VueRouter from 'vue-router';
import router from './router';

Vue.use(VueRouter);

Vue.config.productionTip = false

Vue.filter('duration', val => {
    let mm = Math.floor((val / 60) || 0).toString().padStart(2, '0');
    let ss = Math.floor((val % 60) || 0).toString().padStart(2, '0');
    return mm + ':' + ss;
})

Vue.filter('date', val => {
    let date = new Date(val);
    let yyyy = date.getFullYear();
    let mm = date.getMonth().toString().padStart(2, '0');
    let dd = date.getDay().toString().padStart(2, '0');
    let hh = date.getHours().toString().padStart(2, '0');
    let ii = date.getMinutes().toString().padStart(2, '0');
    let ss = date.getSeconds().toString().padStart(2, '0');
    return `${yyyy}-${mm}-${dd} ${hh}:${ii}:${ss}`;
});

Vue.directive('touch', (el, binding) => {
    let axis = binding.arg.toUpperCase();
    el.style[`overflow${axis}`] = 'scroll';

    el.addEventListener('mousedown', () => {
        el.dataset.touched = 'true';
    })

    window.addEventListener('mousemove', (e) => {
        if (el.dataset.touched === 'true') {
            el.scrollLeft -= e.movementX;
            el.scrollTop -= e.movementY;
        }
    })

    window.addEventListener('mouseup', () => {
        el.dataset.touched = 'false';
    })
})

Vue.prototype.$store = store;

Vue.component('Modal', Modal);

new Vue({
    render: h => h(App),
    router
}).$mount('#app')
