<template>
    <div id="playlist" :style="{width: playlist.open?'400px':'25px'} ">
        <div class="switch" @click="playlist.open = !playlist.open">
            <img src="../assets/menu.png" alt="menu icon" width="20">
        </div>
        <div class="playlist" v-touch:y>
            <div class="music" v-for="(music, i) in musics" :key="i"
                 :class="{active: playlist.music === music && playlist.music === music && music.playing}"
                 @click="play(music)">
                <div class="no">{{i+1}}</div>
                <div class="title">{{music.title}}</div>
                <div class="artist">演唱者：{{music.artists.join(', ') || '不明'}}</div>
                <div class="album">專輯：{{music.albums.join(', ') || '無'}}</div>
                <div class="duration">{{music.duration | duration}}</div>
                <button class="delete-btn" v-show="playlist.music !== music || !music.playing"
                        @click.stop="playlist.remove(music)">x
                </button>
            </div>
        </div>
    </div>
</template>

<script>
    import {mapGetter} from "../store";

    export default {
        name: "Playlist",
        methods: {
            play(music) {
                if (this.playlist.music !== music) {
                    this.playlist.set(music);
                    this.$store.playMusic();
                    this.$router.push(`/playlist/${music.id}`).catch(() => {
                    });
                } else {
                    this.$store.switchPlayStatus();
                }
            },
        },
        computed: {
            ...mapGetter('playlist'),
            musics() {
                return this.playlist.musics;
            }
        }
    }
</script>

<style scoped>
    #playlist {
        display: flex;
        position: relative;
        padding-left: 25px;
        transition: .5s;
        overflow: hidden;
    }

    .switch {
        width: 25px;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        position: absolute;
        top: 0;
        left: 0;
        background-color: #3333;
        cursor: pointer;
        transition: .5s;
    }

    .switch:hover {
        background-color: #5555;
    }

    .playlist {
        width: 400px;
    }

    .music {
        display: grid;
        grid-template:
        "no title duration delete-btn"
        "no artist duration ."
        "no album duration .";
        grid-template-columns: 20px 1fr 80px 40px;
        grid-gap: 5px;
        padding: 5px;
        margin: 5px;
        border: 1px solid #5555;
        align-items: center;
        cursor: pointer;
    }

    .active {
        color: #ff6;
    }


