<template>
    <div id="app">
        <img alt="Vue logo" src="./assets/logo.png" style="grid-area: logo">
        <component v-for="(component,i) in components" :key="i" :is="component"
                   :style="{gridArea: component.name.toLowerCase()}"
                   :id="component.name.toLowerCase()"></component>
        <Modal v-show="$store.modalText" @close="$store.modalText=''">{{$store.modalText}}</Modal>
    </div>
</template>

<script>

    import Musics from "./components/Musics";
    import Albums from "./components/Albums";
    import Player from "./components/Player";
    import PlayerControl from "./components/PlayerControl";
    import PlayerInfo from "./components/PlayerInfo";
    import Playlist from "./components/Playlist";
    import Loading from "./components/Loading";

    export default {
        name: 'App',
        data() {
            return {
                components: [
                    Musics,
                    Albums,
                    Player,
                    PlayerControl,
                    PlayerInfo,
                    Playlist,
                    Loading
                ]
            }
        },
        methods: {
            init(component = this) {
                component.$options.updated = [this.assignGridArea];
                component.$nextTick(this.assignGridArea);
                component.$children.forEach(child => {
                    this.init(child);
                })
            },

            assignGridArea() {
                if (this.$el.querySelectorAll) {
                    this.$el.querySelectorAll('*').forEach(el => {
                        if (el.className) {
                            el.style.gridArea = el.className;
                        }
                    })
                }
            }
        },

        mounted() {
            this.init();
        },
    }
</script>

