import VueRouter from 'vue-router';
import Album from "./components/Album";

let routes = [{
    path: '/'
}, {
    path: '/albums/:albumId/:musicId?',
    component: Album,
    name: 'albums'
}, {
    path: '/playlist/:musicId?/:time?/:status?',
    name: 'playlist'
}];


export default new VueRouter({
    routes,
    mode: 'history',
    base: '/WebExercise/50屆/global/music-player'
});
