<template>
    <div v-if="album">
        <img draggable="false" :src="album.cover_path">
        <div>
            <router-link to="/" tag="button">←</router-link>
            {{album.title}}
            <button class="play-btn" @click="play()">&#x23f5;</button>
        </div>
        <div>建立時間： {{album.created_at | date}}</div>
        <div>演唱者： {{album.artists.join(', ') || '不明'}}</div>
        <div> {{album.musics.length}}首歌 | {{album.duration | duration}}分鐘</div>
        <div> {{album.description}}</div>
        <Modal @close="confirmOpen=false" v-show="confirmOpen">
            確定覆蓋當前的播放清單嗎?
            <div class="btn-group">
                <button class="primary" @click="play(true)">Yes</button>
                <button class="danger" @click="confirmOpen=false">No</button>
            </div>
        </Modal>
    </div>
</template>

<script>
    import {mapGetter} from "../store";

    export default {
        name: "Album",
        data() {
            return {
                confirmOpen: false
            }
        },
        computed: {
            ...mapGetter('playlist'),
            album() {
                let albumId = +this.$route.params.albumId;
                let album = this.$store.albums.find(album => album.id === albumId);
                if (album) {
                    this.setMusics(album);
                }
                return album;
            }
        },
        methods: {
            setMusics(album) {
                this.$store.listMusics = album.musics;
            },
            play(force = false) {
                if (!force && this.playlist.musics.length > 0) {
                    this.confirmOpen = true;
                    return;
                }

                this.confirmOpen = false;
                this.playlist.clear();
                this.album.musics.forEach(music => {
                    this.playlist.add(music);
                });
                this.playlist.set(this.playlist.musics[0]);
                this.$store.playMusic();
            },

        },
        beforeDestroy() {
            this.$store.listMusics = this.$store.musics;
        }
    }
</script>

<style scoped>

</style>
